import { writable, derived } from "svelte/store";
export const cubeParams = writable([]);
export const latestCubeParam = writable([0, 0, 0, 0]); // [index, x, y, angle](normalized)
export const volume = writable(1);
export const addCube = () => cubeParams.update((params) => [...params, { x: -1, y: -1, angle: -1, fx: -1 }]);
export const updateCubeParams = (index, newParams) => {
    cubeParams.update((cubeParams) => {
        const params = [...cubeParams];
        params.splice(index, 1, newParams);
        return params;
    });
    latestCubeParam.set([index, newParams.x, newParams.y, newParams.angle]);
};
